import {
	TableBody,
	TableContainer,
	Table,
	TableRow,
	TableCell,
	MenuItem,
	Select,
} from '@material-ui/core';
import { useFormikContext } from 'formik';
import _get from 'lodash.get';
import Header from './Header';
import {
	TJurisdictionalCost,
	TJurisdictionalCostTable,
} from '../../../../../types';
import { getPayAmount, isDisabledFee } from './helper';

type Props = {
	jurisdictionalCost: TJurisdictionalCost[];
};
export const SettlementTermsTable = ({ jurisdictionalCost }: Props) => {
	const { values, handleChange, handleBlur } =
		useFormikContext<TJurisdictionalCostTable>();

	return (
		<TableContainer>
			<Table aria-labelledby='tableTitle' size='small'>
				<Header />
				<TableBody>
					{jurisdictionalCost.map(item => {
						const payorOptions = _get(item, 'PayorOptions');
						if (typeof item === 'number') return null;
						const itemKey = item.ItemKey;
						return (
							<TableRow key={item.ItemKey}>
								<TableCell align='left'>{item.ItemName}</TableCell>
								<TableCell align='left'>{getPayAmount(item.PayAmount)}</TableCell>
								<TableCell align='left'>{item.Payor}</TableCell>
								<TableCell align='left'>
									<Select
										labelId={itemKey}
										id={itemKey}
										name={itemKey}
										value={values[`${itemKey}`]}
										onChange={handleChange}
										onBlur={handleBlur}
										displayEmpty
										disabled={isDisabledFee(item)}
									>
										<MenuItem value=''>Customary Terms</MenuItem>
										{payorOptions.map(({ Payor: payor, PayorId: payorId }) => (
											<MenuItem key={payorId} value={payorId}>
												{payor}
											</MenuItem>
										))}
									</Select>
								</TableCell>
							</TableRow>
						);
					})}
				</TableBody>
			</Table>
		</TableContainer>
	);
};

export default SettlementTermsTable;
