import _get from 'lodash.get';
import { HeadCell, TJurisdictionalCost } from '../../../../../types';
import config from '../../../../../config';

const CALC_VALUE = 'calc';
const CALC_TEXT = 'Calculated based on Listing Price';
const DISABLED_FEES = ['HOATransferFee', 'HOADocuments'];

export const headCells: HeadCell<TJurisdictionalCost>[] = [
	{
		id: 'ItemName',
		numeric: false,
		disablePadding: false,
		label: 'Charges',
	},
	{
		id: 'PayAmount',
		numeric: false,
		disablePadding: false,
		label: 'Amount* \n(Based on offer price)',
	},
	{
		id: 'Payor',
		numeric: false,
		disablePadding: false,
		label: 'Customary Pays',
	},
];

export const getPayAmount = (value: string) => {
	if (value.toLowerCase() === CALC_VALUE) return CALC_TEXT;
	return value;
};

export const isDisabledFee = (item: TJurisdictionalCost) =>
	DISABLED_FEES.includes(item.ItemKey) ||
	item.Payor === config.defaultText.noValue ||
	item.Payor === config.defaultText.noneValue;

export const getItemsKeys = (jurisdictionalCost: TJurisdictionalCost[]) =>
	jurisdictionalCost.reduce<string[]>(
		(values, jurisdictiona) => [...values, _get(jurisdictiona, 'ItemKey')],
		[]
	);

export const prepareInitValue = (jurisdictionalCost: TJurisdictionalCost[]) =>
	jurisdictionalCost.reduce<{ [key: string]: string }>(
		(values, jurisdictiona) => {
			const itemKey = _get(jurisdictiona, 'ItemKey');
			const value = _get(jurisdictiona, 'PayorIdOverride') || '';
			return { ...values, [`${itemKey}`]: value };
		},
		{}
	);
